.YOUTUBE {
	> .inner {
		article {
			width: 100%;
			padding: 20px 20px;
			border: 1.5px solid #ddd;
			background: #efefef;
			margin-top: 70px;
			margin-bottom: 30px;
			display: flex;
			justify-content: center;
			align-items: center;

			.pic {
				width: calc((100% / 2) - 40px);
				height: 280px;
				margin: 0px auto;
				margin-bottom: 30px;
				:hover {
					box-shadow: 4px 4px 4px 4px rgba(0, 0, 0, 0.5);
					transition: 0.3s;
				}
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
			.boxTxt {
				width: 50%;
				h2 {
					font: bold 25px/1.2 'Poppins', sans-serif;
					color: #222;
					border-bottom: 1px solid #555;
					padding-bottom: 20px;
				}
				p {
					padding-top: 20px;
					font: 15px/1.3 'Poppins', sans-serif;
					color: #666;
					margin-bottom: 40px;
				}
				span {
					font: 12px/1 'Poppins', sans-serif;
					color: #666;
				}
			}
		}
	}
}

@media screen and (max-width: $mobile) {
	.YOUTUBE {
		> .inner {
			article {
				width: 100%;
				padding: 20px 20px;
				border: 1.5px solid #ddd;
				background: #efefef;
				margin-bottom: 30px;
				display: block;

				.pic {
					width: 100%;
					height: 280px;
					margin: 0px auto;
					margin-bottom: 30px;
					:hover {
						box-shadow: 4px 4px 4px 4px rgba(0, 0, 0, 0.5);
						transition: 0.3s;
					}
					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}
				.boxTxt {
					width: 100%;
					h2 {
						font: bold 25px/1.2 'Poppins', sans-serif;
						color: #222;
						border-bottom: 1px solid #555;
						padding-bottom: 20px;
					}
					p {
						padding-top: 20px;
						font: 15px/1.3 'Poppins', sans-serif;
						color: #666;
						margin-bottom: 40px;
					}
					span {
						font: 12px/1 'Poppins', sans-serif;
						color: #666;
					}
				}
			}
		}
	}
}
