.MEMBER {
	> .inner {
		button {
			margin-top: 70px;
			margin-bottom: 10px;
		}
		.join {
			display: flex;
			justify-content: center;
			align-items: center;
			gap: 40px;
			margin-bottom: 80px;

			li {
				font: 16px/1 '맑은고딕';
				color: #333;
			}
		}
		form {
			h2 {
				font: bold 20px/1 '맑은고딕';
				color: #333;
				margin-bottom: 20px;
			}
			.agreement {
				margin-bottom: 50px;
				textarea {
					width: 100%;
					height: 160px;
					box-sizing: border-box;
					padding: 20px;
					margin-bottom: 10px;
					font: 15px/1.8 'Poppins', sans-serif;
					color: #777;
					background: rgba(0, 0, 0, 0.03);
					border: 1px solid #aaa;
					resize: none;
				}

				input {
					margin-left: 10px;
				}
				label {
					font-size: 11px;
					color: #888;
					margin-left: 10px;
				}
			}

			table {
				width: 100%;
				border: none;
				border-top: 2px solid #333;
				border-bottom: 2px solid #333;

				th,
				td {
					border: none;
					padding: 16px 25px;
					font: bold 15px/1 'Poppins', sans-serif;
					color: #555;
				}

				th {
					width: 20%;
					text-align: left;

					&:last-child {
						text-align: center;
						input[type='submit'],
						input[type='reset'] {
							width: 180px;
							height: 30px;
							display: inline-flex;
							justify-content: center;
							align-items: center;
							border: 1px solid #333;
							margin: 0px 10px;
							font: bold 14px/30px 'Poppins', sans-serif;
							color: #555;
							cursor: pointer;
							transition: 0.5s;
							border-radius: 50px;

							&:hover {
								background: #333;
								color: #fff;
							}
						}
					}
				}

				td {
					input[type='text'],
					input[type='password'],
					input[type='email'],
					select,
					textarea {
						width: 60%;
						background: rgba(0, 0, 0, 0.01);
						padding: 10px;
						border: 1px solid #bbb;
						resize: none;
						&:hover {
							background: rgba(0, 0, 0, 0.09);
						}
					}
					input[type='checkbox'],
					input[type='radio'] {
						margin-left: 5px;
						margin-right: 30px;
					}

					> p {
						color: hotpink;
						margin-top: 5px;
						font: 11px/1 '맑은 고딕';
					}
				}

				#comments {
					font-family: '맑은 고딕';
				}
			}
		}
	}
}

@media screen and (max-width: $tablet) {
	.MEMBER {
		.inner {
			.join {
				display: none;
			}
			table {
				th {
					display: none;
					&:last-child {
						display: flex;
						justify-content: center;
						width: 100%;

						input[type='reset'],
						input[type='submit'] {
							display: inline-block;
							width: 48%;
							margin: 0px;
						}
					}
				}
				td {
					width: 100%;

					#userid,
					#pwd1,
					#pwd2,
					#email,
					#edu,
					#comments {
						width: 100%;
					}
				}
			}
		}
	}
}
