* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
ul,
ol,
li {
	list-style: none;
}
a {
	text-decoration: none;
}

fieldset {
	border: none;
}
table {
	border-collapse: collapse;
}
.h {
	position: absolute;
	top: -999999px;
	opacity: 0;
}

body {
	background: rgb(229, 229, 229);
}
